var _this,countDown;

export default {
	data() {
		return {
			phoneData: "", //电话
			amountData: "", //转账金额
			second: 0, //倒计时
			verCode:"", //验证码
		}
	},
	mounted() {
		_this= this;
		clearInterval(countDown);//先清理一次循环，避免缓存
	},
	methods: {
		getVerCode(){
			//获取验证码
			if (!(/^1[3456789]\d{9}$/.test(this.phoneData))) {
			     uni.showToast({
			        icon: 'none',
				    	position: 'bottom',
			        title: '手机号不正确'
			    });
			    return false;
			}
			if(_this.second>0){
				return false;
			}
			_this.second = 60;
			countDown = setInterval(function(){
				_this.second--;
				if(_this.second==0){
					clearInterval(countDown)
				}
			},1000)
			console.log("获取验证码")
		},
		startTransfer() {
			//转账
			if (!(/^1[3456789]\d{9}$/.test(this.phoneData))) {
			     uni.showToast({
			        icon: 'none',
					position: 'bottom',
			        title: '手机号不正确'
			    });
			    return false;
			}
		    if (this.amountData.length === 0) {
		        uni.showToast({
		            icon: 'none',
					      position: 'bottom',
		            title: '请输入转账金额'
		        });
		        return false;
		    }
			if (this.verCode.length != 4) {
			    uni.showToast({
			        icon: 'none',
					position: 'bottom',
			        title: '验证码不正确'
			    });
			    return false;
			}
			console.log("重置密码成功")


		}
	},
	computed:{
		getVerCodeSecond(){
			//验证码倒计时计算
			if(this.second<=0){
				return '获取验证码';
			}else{
				if(this.second<10){
					return '0'+this.second;
				}else{
					return this.second;
				}
			}

		}
	},
}
